#first {
  .name {
    h1 {
      font-family: 'Playfair Display', serif;
      font-weight: bold;
      font-size: 48px;
      line-height: 64px;
      margin: 0;
      margin-bottom: 8px;
    }

    small {
      display: inline-block;
      color: #787878;
      font-size: 20px;
      line-height: 23px;
      margin-bottom: 40px;
    }
  }

  .info {
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 100px;

    & .film {
      background: #FFFFFF;
      border-radius: 3px;
      color: #333333;
      padding: 5px 10px;
      display: inline-block;
      margin-right: 10px;
    }

    & .country-info {
      margin-right: 34px;
    }

    & .play {
      text-decoration: none;

      &:before {
        content: "";
        width: 30px;
        height: 30px;
        background: url("../img/icons/play.svg") no-repeat;
        -webkit-background-size: 100%;
        background-size: 100%;
        margin-right: 15px;
      }
    }

    & .more {
      display: flex;
      align-items: center;
      text-decoration: none;

      &:after {
        content: "";
        width: 30px;
        height: 30px;
        background: url("../img/icons/play.svg") no-repeat;
        -webkit-background-size: 100%;
        background-size: 100%;
        margin-left: 15px;
        transform: rotate(90deg);
      }
    }
  }
}