#second {
  .content {
    background: #FFFFFF;
    border-radius: 3px;
    color: #000000;
    padding: 3% 4%;
  }

  .short-info {
    margin-right: 3%;

    .button-content {
      background: #FF8F27;
      color: #FFFFFF;
      padding: 10px 40px;
      border: none;
      font-size: 16px;
      line-height: 16px;
      margin-bottom: 20px;
    }

    .poster {
      display: block;
      margin-top: -40%;
      margin-bottom: 20px;
    }

    .actors-title {
      font-weight: 900;
      font-size: 16px;
      line-height: 150%;
      padding-left: 15px;
      margin-bottom: 10px;
    }

    .actors-list {
      list-style: none;
      padding-left: 15px;
      text-decoration: underline;
      margin: 0;
      font-size: 16px;
      line-height: 150%;
    }
  }

  .full-info{
    flex: 0.97;

    .nav-menu {
      margin-bottom: 4%;

      a {
        color: #000000;
        text-decoration: none;

        font-size: 18px;
        line-height: 21px;
      }

      & .menu-active {
        color: #FF8F27;
      }

      & a:hover {
        color: #FF8F27;
      }
    }

    .menu-content {
      margin-right: 50px;
      font-size: 16px;
      line-height: 140%;
      margin-top: 0;
      display: none;

      h2 {
        font-weight: 900;
        font-size: 16px;
        line-height: 140%;
      }
    }

    .visible {
      display: block;
    }

    .asside {

      .form {
        margin-bottom: 48px;
      }

      h3 {
        font-size: 16px;
        font-weight: 900;
        line-height: 150%;
        margin-top: 0;
        margin-bottom: 14px;
      }

      .input-form {
        background: rgba(196, 196, 196, 0.5);
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
        padding: 12px 18px;
        border: none;
        color: #AEAEAE;
        flex: 1;
      }

      .button-form {
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        background: #FF8F27;
        border: none;
        padding: 12px 17px;
      }
    }
  }
}