body {
  background: fixed url("/img/main-bg.png") top center no-repeat #0A090A;
  -webkit-background-size: 100%;
  background-size: 100%;
  color: #ffffff;
  line-height: 16px;
  font-size: 14px;
  font-family: 'Roboto', sans-serif;

  a, a:hover {
    color: #ffffff;
  }
}

@media (max-width: 1200px) {
  .container {
    max-width: 960px;
  }

  .nav-menu a{
    font-size: 16px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 740px;
  }

  .nav-menu {
    flex-wrap: wrap;

    & a {
      flex-basis: 25%;
      line-height: 2;
    }
  }

  .full-content {
    flex-wrap: wrap;
  }

  .menu-content {
    margin-bottom: 4%;
  }

  p, .actors-list {
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 540px;
  }

  #first a.more {
    display: none !important;
  }

  .nav-menu {
    & a {
      flex-basis: 45%;
      line-height: 2;
    }
  }

  #second .menu-content{
    margin-right: 10px !important;
  }
}

@media (max-width: 568px) {
  .container {
    max-width: 90%;
  }

  #header .search {
    flex: 1 !important;
    flex-basis: 100% !important;
    order: 3;
    margin-top: 5%;
  }

  .content {
    flex-wrap: wrap;
  }

  .actors-list, .actors-title {
    display: none;
  }

  .short-info {
    //margin-bottom: 5%;
  }

  .full-info {
    flex-wrap: wrap;
  }

  .play {
    flex-basis: 100%;
    margin-top: 5%;
  }

  #second .short-info .poster {
    display: inline-block !important;
    width: 100%;
    margin-top: -15% !important;
  }

  #second .short-info {
    margin-right: 0%;
  }

  #second .short-info .button-content {
    width: 100% !important;
  }
}