#header {
  padding-top: 45px;
  margin-bottom: 30%;

  .logo a {
    font-weight: 900;
    font-size: 24px;
    text-decoration: none;
  }

  .search {
    display: flex;
    flex: 0.8;

    & .search-input {
      background: rgba(196, 196, 196, 0.5);
      border-radius: 5px;
      padding: 12px 18px;
      border: none;
      color: #AEAEAE;
      flex: 1;

      &::placeholder {
        color: #AEAEAE;
      }
    }

    .search-button {
      background-color: transparent;
      border: none;
      padding: 10px 18px;
    }
  }

  .user {
    & .user-avatar {
      margin-left: 20px;
      border-radius: 50%;
      width: 40px;
      height: 40px;
      object-fit: cover;
    }
    & .bookmark:hover {
      cursor: pointer;
    }
  }
}